import { zipcodeActions } from "redux/slices/zipcode/zipcodeSlice";
import { formLoaderActions } from "redux/slices/formLoader";
import http from "./http.service";
import { AppDispatch } from "redux/store";

import { storeActions } from "redux/slices/store/storeSlice";

import Promisable from "./promisable.service";
import { NavigateFunction } from "react-router-dom";
import { modalActions } from "redux/slices/modal";

const url = "/store";

const StoresService = {
  getStorebyID: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/${id}`)
    );

    if (success) {
      const { store } = success.data.data;
      dispatch?.(storeActions.setStore(store));
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },

  getStores: async (data: any, dispatch?: AppDispatch) => {
    dispatch?.(storeActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}/filter`, data)
    );

    if (success) {
      const { stores } = success.data.data;
      dispatch?.(storeActions.setStores(stores));
    }

    dispatch?.(storeActions.setLoading(false));

    return [success, error];
  },

  addStore: async (
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(storeActions.setLoading(true));
    http.setJWT();
    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}`, data)
    );
    if (success) {
      const store = success.data.data.store;
      dispatch?.(storeActions.addStore(store));
      navigate?.("/store-management");
    }
    dispatch?.(storeActions.setLoading(false));
    return [success, error];
  },

  deleteStore: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(modalActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.delete(`${url}/${id}`)
    );

    if (success) {
      dispatch?.(modalActions.closeModal());
      dispatch?.(storeActions.deleteStore(id));
    }
    dispatch?.(modalActions.setLoading(false));
    return [success, error];
  },

  updateStore: async (
    id: string,
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));
    http.setJWT();
    const [success, error]: any = await Promisable.asPromise(
      http.put(`${url}/${id}`, data)
    );
    if (success) {
      const store = success.data.data;
      dispatch?.(storeActions.updateStore({ id, store }));
      navigate?.("/store-management");
    }
    dispatch?.(formLoaderActions.setLoading(false));
    return [success, error];
  },
};

export default StoresService;
