import { NavigateFunction } from "react-router-dom";
import { zipcodeActions } from "./../redux/slices/zipcode/zipcodeSlice";
import http from "./http.service";
import { AppDispatch } from "redux/store";
import Promisable from "./promisable.service";
import { modalActions } from "redux/slices/modal";
import { formLoaderActions } from "redux/slices/formLoader";

const url = `/zipcode`;

const ZipCodeService = {
  getZipCodebyID: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/${id}`)
    );

    if (success) {
      const { zipcode } = success.data.data;
      dispatch?.(zipcodeActions.setZipCode(zipcode));
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },

  getZipCodes: async (data: any, dispatch?: AppDispatch) => {
    dispatch?.(zipcodeActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}/filter`, data)
    );

    if (success) {
      const { zipcodes } = success.data.data;
      dispatch?.(zipcodeActions.setZipCodes(zipcodes));
    }

    dispatch?.(zipcodeActions.setLoading(false));

    return [success, error];
  },
  getZipCodesRequest: async (dispatch?: AppDispatch) => {
    dispatch?.(zipcodeActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/all-requested-zipcodes`)
    );

    if (success) {
      const { requestedZipCodes } = success.data.data;
      dispatch?.(zipcodeActions.setZipCodesRequest(requestedZipCodes));
    }

    dispatch?.(zipcodeActions.setLoading(false));

    return [success, error];
  },
  getMostRequestedZipCodes: async (dispatch?: AppDispatch) => {
    dispatch?.(zipcodeActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/requested-zipcodes-group`)
    );

    if (success) {
      const { requestedZipCodes } = success.data.data;
      dispatch?.(zipcodeActions.setmostRequestZipCodes(requestedZipCodes));

      console.log("our bje are ======", requestedZipCodes);
    }

    dispatch?.(zipcodeActions.setLoading(false));

    return [success, error];
  },

  createZipCodes: async (
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}`, data)
    );

    if (success) {
      navigate?.("/zip-code-management");
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },

  deleteZipCodes: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(modalActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.delete(`${url}/${id}`)
    );

    if (success) {
      dispatch?.(modalActions.closeModal());
      dispatch?.(zipcodeActions.filterZipCode(id));
    }

    dispatch?.(modalActions.setLoading(false));

    return [success, error];
  },

  updateZipCode: async (
    id: string,
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));
    http.setJWT();
    const [success, error]: any = await Promisable.asPromise(
      http.put(`${url}/${id}`, data)
    );
    if (success) {
      const zipcode = success.data.data;
      dispatch?.(zipcodeActions.updateZipCode({ id, zipcode }));
      navigate?.("/zip-code-management");
    }
    dispatch?.(formLoaderActions.setLoading(false));
    return [success, error];
  },
};

export default ZipCodeService;
