import Button from "components/atoms/Button";
import { MODAL, modalActions } from "redux/slices/modal";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import ZipCodeService from "services/zipcode.service";
import BannerService from "services/banner.service";
import FaqService from "services/faq.service";
import CampaignService from "services/campaigns.service";
import CategoriesService from "services/categories.service";
import CityService from "services/city.service";
import ProductsService from "services/products.service";
import RoleService from "services/role.service";
import { SubCategoryService } from "services/subcategories.service";
import StoresService from "services/store.service";
import AuthService from "services/auth.service";

export default function ConfirmationForm() {
  const dispatch = useAppDispatch();
  const data = useAppSelector((state) => state.modal.data);

  const onClickYes = () => {
    if (data.type === MODAL.DELETE_ZIPCODE)
      ZipCodeService.deleteZipCodes(data.id, dispatch);
    else if (data.type === MODAL.DELETE_CITY)
      CityService.deleteCity(data.id, dispatch);
    else if (data.type === MODAL.DELETE_BANNER)
      BannerService.deletebanner(data.id, dispatch);
    else if (data.type === MODAL.DELETE_FAQ)
      FaqService.deleteFaq(data.id, dispatch);
    else if (data.type === MODAL.DELETE_CAMPAIGN)
      CampaignService.deleteCampaign(data.id, dispatch);
    else if (data.type === MODAL.DELETE_CATEGORY)
      CategoriesService.deleteCategory(data.id, dispatch);
    else if (data.type === MODAL.DELETE_SUBCATEGORY)
      SubCategoryService.deleteSubCategory(data.id, dispatch);
    else if (data.type === MODAL.DELETE_PRODUCT)
      ProductsService.deleteProduct(data.id, dispatch);
    else if (data.type === MODAL.DELETE_PRODUCT_MEDIA)
      ProductsService.updateProduct(data.id, { images: data.images }, dispatch);
    else if (data.type === MODAL.DELETE_PRODUCT_TAG)
      ProductsService.updateProduct(data.id, { tags: data.tags }, dispatch);
    else if (data.type === MODAL.DELETE_PRODUCT_CATEGORY)
      ProductsService.updateProduct(
        data.id,
        { categories: data.categories },
        dispatch
      );
    else if (data.type === MODAL.DELETE_ROLE)
      RoleService.deleteRole(data.id, dispatch);
    else if (data.type === MODAL.DELETE_STORE)
      StoresService.deleteStore(data.id, dispatch);
    else if (data.type === MODAL.DELETE_USER)
      AuthService.deleteUser(data.id, dispatch);
  };

  return (
    <div>
      <h3 style={{ marginTop: 0 }}>{data.heading}</h3>
      <p>{data.message}</p>
      <Button
        variant="outlined"
        sx={{ marginRight: "10px" }}
        onClick={() => dispatch(modalActions.closeModal())}
      >
        No
      </Button>
      <Button variant="contained" onClick={onClickYes}>
        Yes
      </Button>
    </div>
  );
}
